.header {
    position: fixed;
    left: 0;
    right: 0;
    top: 8px;
    margin: 0 auto;
    width: 100%;
    height: 96px;
    max-width: 1256px;

    .menu {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 40px;
        height: 100%;

        ul {
            display: flex;

            li {
                a {
                    font-size: 14px;
                    line-height: 22px;
                    color: $brown-dark;
                }

                + li {
                    margin-left: 32px;
                }
            }
        }
    }

    &__buttons {
        display: flex;

        .button {
            &:last-child {
                margin-left: 18px;
            }
        }
    }
}